%center-and-max-height {
  height: 100%;
  display: flex;
  align-items: center;
}

%expression-and-result {
  overflow: hidden;
  padding-left: 0.5rem;
  cursor: pointer;

  &:hover {
    background-color: #d0d3df;
  }
}

#display {
  position: relative;
  width: 100%;
  height: 20%;
  font-family: Lato, serif;
  font-size: 1.2rem;
  background-color: #fbfcff;

  .result-container {
    position: absolute;
    width: 100%;
    height: 100%;
    font-family: inherit;
    font-size: inherit;
    z-index: 3;
    display: flex;
    flex-direction: row;
    align-items: center;

    .expression {
      @extend %center-and-max-height;
      @extend %expression-and-result;
      flex: 1 1 65%;
    }

    .equals {
      @extend %center-and-max-height;
      flex: 0 0 1rem;
      padding: 0 0.5rem;
    }

    .result {
      @extend %center-and-max-height;
      @extend %expression-and-result;
      flex: 1 1 35%;
    }
  }

  #display-title {
    position: absolute;
    top: 0.5rem;
    left: 0.5rem;
    z-index: 4;
    color: #4a6275;
    font-size: 1rem;
    margin: 0;
    padding: 0;
  }

  #malformed-expression {
    position: absolute;
    bottom: 0.5rem;
    left: 0.5rem;
    z-index: 3;
    color: #812519;
    font-size: 0.9rem;
    margin: 0;
    padding: 0;
  }

  @media (max-width: 400px) {
    font-size: 1.1rem;

    #display-title {
      font-size: 0.8rem;
      top: 0.2rem;
      left: 0.2rem;
    }

    #malformed-expression {
      font-size: 0.7rem;
      bottom: 0.2rem;
      left: 0.2rem;
    }
  }
}
